	const app = getApp()
export default {
	data() {
		return {
			object: [],
			index: 0,
			classIndex: 0,
			type: '',
			orderConvert: {},
			userName: '',
			classify: [],
			num: '',
			userId: '',
			inputValNum: ''
		};
	},
	onLoad(option) {
		this.type = option.type;
		
		if(option.type == '批量'){
			this.getObject()
		}else{
			this.orderConvert = JSON.parse(option.obj)
			this.id = this.orderConvert.id
		}
	},
	methods: {
		inputVal(e){
			if( e.detail.value > this.num){
				uni.showToast({
					title: '不能大于剩余数量',
					icon: 'none'
				})
			}else if( e.detail.value < 1){
				uni.showToast({
					title: '赠送数量不能小于1',
					icon: 'none'
				})
			}
			this.inputValNum = e.detail.value
		},
		bindPickerChange(e) {
			this.index = e.target.value;
			this.classify_id = this.object[this.index].id
			this.getClassify()
		},
		bindChange(e){
			this.classIndex = e.target.value;
			this.num =  this.classify[this.classIndex].num
			console.log(this.num)
			this.id =  this.classify[this.classIndex].id
		},
		getObject(){
			uni.request({
				url: `${this.$helper.requestUrl}user/getClassifyList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.object = res.data
						this.classify_id =res.data[0].id
						this.getClassify()
					} else {
		
					}
				}
			});
		},
		getClassify(){
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}user/getCurriculaList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					classify_id: this.classify_id
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.classify = res.data
						this.num =  this.classify[0].num
						this.id = this.classify[0].id
					} else {
			
					}
				}
			});
		},
		telName(e){
			console.log(e)
			this.inputVal = e.detail.value
			this.getName()
		},
		getName(){
			uni.request({
				url: `${this.$helper.requestUrl}user/getUserByMobile`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					mobile: this.inputVal
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '下单信息');
					uni.hideToast()
					if (res.status_code == 'ok') {
						this.userName = res.data.nickname
						this.userId = res.data.id
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		give(){
			if(this.type == '批量'){
				if(this.inputValNum == ''){
					uni.showToast({
						title: '请输入赠送数量',
						icon: 'none'
					})
					return false
				}
			}else{
				this.inputValNum = ''
			}
			if(this.userId == ''){
				uni.showToast({
					title: '请选择赠送用户',
					icon: 'none'
				})
				return false
			}
			uni.request({
				url: `${this.$helper.requestUrl}user/sendExchangeCode`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					uid: this.userId,
					id: this.id,
					num: this.inputValNum
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '');
					uni.hideToast()
					if (res.status_code == 'ok') {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
						setTimeout(e => {
							uni.navigateBack({
								data: 1
							})
						}, 2000)
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		
		
	}
};
